.hero-container {
  /* background: url('/public/assets/home/students_1.png') center center/cover no-repeat; */
  height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.3);
  object-fit: contain;
}

.hero-container > h1 {
  color: #fff;
  font-size: 100px;
  margin-top: -150px;
  margin-bottom: 200px;
}

@media only screen and (max-width: 480px) {
  .hero-container {
    background-position-x: center;
  }
  .hero-container > h1 {
    color: #fff;
    font-size: 10px;
    margin-top: -70px;
  }
  .hero-container > p {
    font-size: 15px;
  }
}

.hero-container > p {
  margin-top: 8px;
  color: #fff;
  font-size: 22px;
  text-align: center;
  font-family: "Trebuchet MS", "Lucida Sans Unicode", "Lucida Grande",
    "Lucida Sans", Arial, sans-serif;
}

.hero-btns {
  margin-top: 32px;
}

.hero-btns .btn {
  margin: 6px;
}

.fa-play-circle {
  margin-left: 4px;
}

@media screen and (max-width: 960px) {
  .hero-container {
    background-position-x: center;
  }
  .hero-container > h1 {
    font-size: 70px;
    margin-top: -150px;
  }
}

@media screen and (max-width: 768px) {
  .hero-container {
    background-position-x: center;
  }
  .hero-container > h1 {
    font-size: 40px;
    margin-top: -100px;
    margin-bottom: 50px;
  }

  .hero-container > p {
    font-size: 20px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}

@media screen and (max-width: 400px) {
  .hero-container {
    background-position-x: center;
  }
  .hero-container > h1 {
    font-size: 30px;
    margin-top: -60px;
    margin-bottom: 100px;
  }

  .hero-container > p {
    font-size: 15px;
  }

  .btn-mobile {
    display: block;
    text-decoration: none;
  }

  .btn {
    width: 100%;
  }
}
