.contain {
  background-image: url("/public/assets/faq_section/faq_img.png");
  background-position: center;
  box-shadow: inset 0 0 0 1000px rgba(26, 26, 26, 0.6);
  background-repeat: no-repeat;
  background-size: cover;
  color: #ffffff;
  padding-bottom: 50px;
  font-size: 40px;
}

.quote_image {
  width: 80px;
  height: 80px;
  align-items: flex-start;
}

.testimonialContainer {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  overflow: auto;
  flex-wrap: wrap;
}

.testimonials {
  display: flex;
  flex-direction: row;
  width: 40%;
  height: auto;
  border-radius: 20px;
  box-shadow: 0 0 15px -6px rgb(255, 255, 255);
  margin: 30px 30px;
  text-align: center;
  color: black;
  font-size: 30px;
  padding: 10px;
}

.content_testimonials {
  display: flex;
  flex-direction: column;
  margin: 10px 20px;
  text-align: justify;
  color: #ffffff;
}

.testimonial_name {
  margin-bottom: 10px;
}

.testimonial_comments {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  font-size: 20px;
  color: rgb(255, 255, 255);
}

.testimonials_date {
  color: #7a7a7a;
  margin-top: 5px;
  font-size: 15px;
}

.lastname {
  font-family: "Inconsolata", monospace;
  font-weight: 200;
  font-style: normal;
  font-size: 20px;
}

@media only screen and (max-width: 768px) {
  .contain {
    padding-top: 30px;
    padding-bottom: 10px;
    font-size: 20px;
  }

  .testimonials {
    font-size: 20px;
  }

  .quote_image {
    width: 30px;
    height: 30px;
  }

  .testimonial_comments {
    font-size: 12px;
  }

  .content_testimonials {
    margin: 5px 10px;
  }

  .testimonials_date {
    margin-top: 5px;
    font-size: 10px;
  }
}

@media only screen and (max-width: 1024px) {
  .contain {
    padding-top: 30px;
    padding-bottom: 10px;
    font-size: 25px;
  }

  .testimonials {
    width: 100%;
    height: auto;
    font-size: 25px;
    border-radius: 20px;
    margin: 20px 30px;
  }

  .quote_image {
    width: 40px;
    height: 40px;
  }

  .testimonial_comments {
    font-size: 17px;
  }

  .content_testimonials {
    margin: 10px 10px;
  }

  .testimonials_date {
    margin-top: 5px;
    font-size: 13px;
  }
}
